export default {
	data() {
		return {
			isShow: false
		};
	},
	onLoad() {},
	methods: {
		showNavloading() {
			const self = this;
			self.isShow = !self.isShow;
			if (self.isShow) {
				uni.showNavigationBarLoading();
			} else {
				uni.hideNavigationBarLoading();
			}
		},
		tip() {
			uni.showToast({
				title: '测试iconImage',
				image: '/static/582.jpg'
			});
		},
		setnavTitle() {
			uni.setNavigationBarTitle({
				title: '新设置的标题'
			});
		},
		setnavColor() {
			uni.setNavigationBarColor({
				setnavColor: '#2ade95',
				backgroundColor: '#DD524D',
				animation: { duration: 800, timingFunc: 'easeIn' }
			});
		}
	}
};
